import React, { useState, FormEvent, useEffect, useCallback } from 'react';
import {
  Container,
  Navbar,
  Nav,
  Row,
  Form,
  FormControl,
  Button,
} from 'react-bootstrap';

import GitHubUser from '../../components/GitHubUser';
import GitHubContacts from '../../components/GitHubContacts';

import { useToast } from '../../hooks/toast';
import Util from '../../utils/Utils';

import GitHubRepository, {
  IGitHubProfileDTO,
  IGitHubContactDTO,
  IGitHubRateLimitsDTO,
} from '../../repositories/GitHubRepository';

import './styles.css';

const Landing: React.FC = () => {
  const githubRepository = new GitHubRepository();

  const [rateLimits, setRateLimits] = useState<IGitHubRateLimitsDTO>({
    limit: 0,
    remaining: 0,
    reset: 0,
  });

  const [username, setUsername] = useState('');
  const [isValidUser, setIsValidUser] = useState<boolean>();
  const [profile, setProfile] = useState<IGitHubProfileDTO | null>();
  const [following, setFollowing] = useState<IGitHubContactDTO[]>([]);
  const [followers, setFollowers] = useState<IGitHubContactDTO[]>([]);
  const [notFollowers, setNotFollowers] = useState<IGitHubContactDTO[]>([]);

  const { addToast } = useToast();
  const util = new Util();

  const updateRateLimits = useCallback(async () => {
    const limits = await githubRepository.getRateLimits();
    setRateLimits(limits);
  }, [githubRepository]);

  const handleIndexUser = useCallback(
    async (e: FormEvent) => {
      e.preventDefault();

      try {
        await githubRepository.getProfile(username).then(response => {
          setProfile(response);
          setFollowing([]);
          setFollowers([]);
          setIsValidUser(true);
        });

        await githubRepository
          .getContacts(username, 'following', 1)
          .then(response => setFollowing(response));

        await githubRepository
          .getContacts(username, 'followers', 1)
          .then(response => setFollowers(response));
      } catch (error) {
        updateRateLimits();
        setProfile(null);
        setFollowing([]);
        setFollowers([]);
        setIsValidUser(false);

        const statusCode = error.response.status;

        if (statusCode === 403)
          addToast({
            title: 'Requests limit exceeded!',
            description: `You have reached the limit of ${
              rateLimits.limit
            } requests per hour! Try again in ${util.calculateMinutesLeft(
              rateLimits.reset,
            )} minutes.`,
            style: 'text-warning',
          });

        if (statusCode === 404)
          addToast({
            title: 'User not found!',
            description: `User "${username}" not found on GitHub!`,
            style: 'text-warning',
          });
      }
    },
    [
      addToast,
      githubRepository,
      rateLimits.limit,
      rateLimits.reset,
      updateRateLimits,
      username,
      util,
    ],
  );

  useEffect(() => {
    updateRateLimits();
  }, [profile]);

  useEffect(() => {
    const response = githubRepository.getNotFollowers(following, followers);
    setNotFollowers(response);
  }, [following, followers]);

  return (
    <Container fluid>
      <Navbar
        collapseOnSelect
        className="navbar-virtual-width"
        expand="xl"
        fixed="top"
        bg="dark"
        variant="dark"
      >
        <Navbar.Brand className="nav-brand" href="#profile">
          GitHub - Follow Me (or Not)
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          {profile && (
            <Nav className="mr-auto">
              <Nav.Link href="#following">Following</Nav.Link>
              <Nav.Link href="#followers">Followers</Nav.Link>
              <Nav.Link href="#not-following-back">Not following back</Nav.Link>
            </Nav>
          )}
          <Navbar.Text className="ml-auto text-muted">
            <small>{`Remaining ${rateLimits.remaining} of ${rateLimits.limit}`}</small>
          </Navbar.Text>
          <Form
            className="ml-auto"
            onSubmit={handleIndexUser}
            validated={isValidUser}
            noValidate
            inline
          >
            <FormControl
              type="text"
              className="mr-sm-2 user-form-input"
              onChange={e => {
                setUsername(e.target.value);
              }}
              onSubmit={handleIndexUser}
              placeholder="Enter a GitHub username."
              autoFocus
              required
            />
            <Button
              type="submit"
              className="user-form-button"
              variant="outline-info"
              disabled={!username}
            >
              Search
            </Button>
          </Form>
        </Navbar.Collapse>
      </Navbar>
      <Row className="content-wrapper" md={2} lg={3} xl={4}>
        {profile && (
          <>
            <GitHubUser anchorId="profile" profile={profile} />
            <GitHubContacts
              title="Following"
              anchorId="following"
              contacts={following}
            />
            <GitHubContacts
              title="Followers"
              anchorId="followers"
              contacts={followers}
            />
            <GitHubContacts
              title="Not following back"
              anchorId="not-following-back"
              contacts={notFollowers}
            />
          </>
        )}
      </Row>
    </Container>
  );
};

export default Landing;
